	module.exports = {"data":{"markdownRemark":{"html":"<p>What was covered during the Meetup:</p>\n<ul>\n<li>What React can and can not do</li>\n<li>React.JS fundamental concepts, State, Props</li>\n<li>Live React.JS coding, examples, core concepts in practice</li>\n<li>Building a Job Board using React and Github Jobs API (adding it to LACoders.tech?)</li>\n<li>MobX, state management theory (this will covered during next meetings)</li>\n</ul>\n<p><code>React</code> is a an open-source Javascript library for building user interfaces. The group used <code>create-react-app</code> package to generate a React application boilerplate. After discussing what React is, what it can and can not do, the group dived deeper into some of the fundamental concepts of React, <code>JSX</code>, <code>state</code>, <code>props</code>, and <code>lifecycle methods</code>. After exploring different React code samples, we developed a Job Board website using Github Jobs API. The code is available (MIT license) in our Github organization page for everyone to see and use. You can find it <a href=\"https://github.com/LACoders/JobBoard-React101\" target=\"_blank\">here</a>.</p>\n<p>During our next meetups, we will dive deeper into the world of React and build more complex and cool stuff with it.</p>\n<img src=\"https://secure.meetupstatic.com/photos/event/d/9/2/b/600_472255595.jpeg\" alt=\"los angeles coders\" style=\"width: 100%; max-height: 500px;\" />","frontmatter":{"title":"Introduction to React | Summary of June 21st's Meetup @ WeWork West Hollywood","author":"Argi Avetisyan","date":"24 June, 2018"}}},"pathContext":{"slug":"/2018-06-24-introduction-to-react/"}}

